@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    margin: 5rem auto 0 auto;
    width: 90%;

    @include m.tablet {
        // margin: 5rem auto 0 1rem;
        width: 50%;
    }

}

.campo {
    display: flex;
    margin-bottom: 2rem;
    flex-direction: column;

    label {
        flex: 0 0 5rem;
    }

    @include m.tablet {
        align-items: center;
        flex-direction: row;
        label {
            flex: 0 0 10rem;
        }
    }


    input {
        flex: 1;
        border: 1px solid;
        padding: calc(v.$separacion / 4);
        border-radius: 1rem;

        &:disabled {
            background-color: lighten(v.$negro, 30%);
            color: v.$negro;
            cursor: not-allowed;
        }
    }

    select {
        width: 100%;
        border: 1px solid;
        padding: 1rem;
        border-radius: 1rem;
    }
}

.centrar {
    display: flex;
    justify-content: center;
}