/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #2c2a2a;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #2c2a2a;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #2c2a2a;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
    overflow-x: hidden;
  }
}
.contenedor-app .app::-webkit-scrollbar {
  display: none;
}

.barra {
  margin: 5rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

.mensaje-imagen {
  display: flex;
}
.mensaje-imagen img {
  margin: 1rem auto;
}

.main-img {
  margin: 2rem;
}
@media (min-width: 768px) {
  .main-img {
    margin: 2rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.inicio {
  margin: 2rem;
}

.info {
  padding: 3rem 2rem;
}

.pasos {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2.5rem;
}
.pasos .flecha {
  transform: rotate(180deg);
  width: 25%;
}
.pasos .card h4 {
  text-align: center;
}
@media (min-width: 768px) {
  .pasos {
    display: flex;
    flex-direction: row;
    gap: 3rem;
  }
  .pasos .flecha {
    transform: rotate(90deg);
    width: 5%;
  }
}

.entradas-blog {
  display: flex;
  flex-direction: column;
  margin: 5rem 2rem;
  gap: 2rem;
  color: #2c2a2a;
}
@media (min-width: 768px) {
  .entradas-blog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.entradas-blog .card {
  padding: 2rem;
  background-color: #e1e1e1;
  border-radius: 1rem;
}

.centros-info {
  margin-top: 5rem;
}
.centros-info h3 {
  margin-top: 5rem;
}
.centros-info .centro-1 {
  margin-left: 2rem;
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .centros-info .centro-1 {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  .centros-info .centro-1 img {
    width: 70%;
    margin: 0 auto;
  }
}
.centros-info .centros {
  margin-left: 2rem;
  margin-right: 2rem;
  display: flex;
  gap: 1rem;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  margin: 0 1rem;
  background-color: #e1e1e1;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background-color: #56BAB2;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio:hover {
  transform: scale(1.02);
}
.listado-servicios .servicio p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #00A5B2;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.contenido-resumen span {
  color: #00A5B2;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid #e1e1e1;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.citas, .servicios {
  margin-top: 5rem;
  padding: 0;
}
@media (min-width: 768px) {
  .citas, .servicios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
  }
}
.citas div, .servicios div {
  background-color: #56BAB2;
  border-radius: 1rem;
  margin: 2rem 1rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .citas div, .servicios div {
    margin: 0rem;
  }
}
.citas div p, .servicios div p {
  font-weight: 700;
  color: #2c2a2a;
}
.citas div span, .servicios div span,
.citas div .servicio,
.servicios div .servicio {
  font-weight: 400;
  color: #2c2a2a;
}

.servicios {
  margin: 1rem;
}
.servicios .acciones {
  margin: 0;
}
.servicios .acciones a,
.servicios .acciones input {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .servicios .acciones a,
.servicios .acciones input {
    margin: 0;
  }
}
.servicios .boton,
.servicios .boton-eliminar {
  margin: 0 0 1.5rem 0;
}

.header {
  background-color: #7AB52D;
  padding: 1rem 0;
  width: 100%;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  margin: 0;
}
.barra .menu-hamburguesa {
  width: 5rem;
  filter: invert(100%);
}
.barra .menu-hamburguesa:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
  .barra .menu-hamburguesa {
    display: none;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.izquierda {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .izquierda {
    align-items: flex-end;
    flex-direction: row;
  }
}
.izquierda img {
  max-width: auto;
}
.izquierda a {
  width: 15vh;
}
.izquierda h1 {
  text-transform: uppercase;
  color: #FFFFFF;
}

.navegacion {
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  margin: 3rem 0 0 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
    margin: 0;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:hover {
    color: #1a1b15;
  }
}

/**Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.ver {
  display: none;
  visibility: visible;
  opacity: 1;
  height: auto;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .ver {
    display: flex;
  }
}

.footer {
  padding: 2.5rem 0 0 0;
  background-color: #7AB52D;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .navegacion a {
  margin-bottom: 1.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.form {
  display: flex;
  justify-content: center;
}

@media (min-width: 768px) {
  .opciones {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
  }
}
.opciones img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 80%;
  max-height: 50%;
}

.pdf {
  width: 800px;
  margin: 5rem auto;
}

h4 {
  text-align: center;
}

.encabezado {
  display: flex;
}

.detalle-citas .titulos {
  display: flex;
  background-color: #e1e1e1;
  justify-content: space-evenly;
}

.formulario {
  margin: 5rem auto 0 auto;
  width: 90%;
}
@media (min-width: 768px) {
  .formulario {
    width: 50%;
  }
}

.campo {
