}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
