@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    padding: v.$separacion / 2 0 0 0;
    background-color: v.$verdoso;
    margin-bottom: 0;

    .contenido-footer {
        padding: v.$separacion / 2;
    }

    .navegacion {
        visibility: visible;
        opacity: 1;
        height: auto;
        a {
            margin-bottom: 1.5rem;
        }
    }

    .copyright {
        color: v.$blanco;
        text-align: center;
    }
}