@use 'base/variables' as v;
@use 'base/mixins' as m;

.citas {
    margin-top: v.$separacion;
    padding: 0;

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 1.5rem;
    }

    div {
        background-color: v.$celeste;
        border-radius: 1rem;
        margin: 2rem 1rem;
        padding: 1rem;

        @include m.tablet {
            margin: 0rem
        }

        p {
            font-weight: v.$bold;
            color: v.$grisOscuro;
        }

        span,
        .servicio {
            font-weight: v.$regular;
            color: v.$grisOscuro;
        }
    }
}

.servicios {
    @extend .citas;
    margin: 1rem;

    .acciones {
        margin: 0;

        a,
        input {
            margin-top: 1rem;

            @include m.tablet {
                margin: 0;
            }
        }

    }

    .boton,
    .boton-eliminar {
        margin: 0 0 1.5rem 0;
    }
}