@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .app {
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;

        @include m.tablet {
            overflow: scroll;
            // overflow-y: hidden;
            overflow-x: hidden;
        }
    }

    .app::-webkit-scrollbar {
        display: none;
    }
}

.barra {
    margin: v.$separacion;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .boton {
        @include m.boton(v.$negro, v.$blanco);
        margin: 0;
    }
}

.barra-servicios {
    margin-bottom: v.$separacion;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    .boton {
        margin: 0 0 1rem 0;
    }
}

.mensaje-imagen {
    display: flex;

    img {
        margin: 1rem auto;
    }
}

.main-img {
    margin: 2rem;

    @include m.tablet {
        margin: 2rem;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
    }

}

.inicio {
    margin: 2rem;
}

.info {
    padding: 3rem 2rem;

}

.pasos {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: calc(v.$separacion / 2);

    .flecha {
        transform: rotate(180deg);
        width: 25%;
    }

    .card {
        h4 {
            text-align: center;
        }
    }

    @include m.tablet {
        display: flex;
        flex-direction: row;
        gap: 3rem;

        .flecha {
            transform: rotate(90deg);
            width: 5%;
        }
    }


}

.entradas-blog {
    display: flex;
    flex-direction: column;
    margin: v.$separacion 2rem;
    gap: 2rem;
    color: v.$grisOscuro;
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }


    .card {
        padding: 2rem;
        background-color: v.$gris;
        border-radius: 1rem;
    }
}

.centros-info {
    margin-top: 5rem;

    h3 {
        margin-top: v.$separacion;
    }

    .centro-1 {
        margin-left: 2rem;
        margin-right: 2rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 2fr;

            img {
                width: 70%;
                margin: 0 auto;
            }
        }
    }

    .centros {
        margin-left: 2rem;
        margin-right: 2rem;
        display: flex;
        gap: 1rem;
    }
}