@use 'base/variables' as v;
@use 'base/mixins' as m;

.opciones {

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 1rem;
    }


    img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        max-width: 80%;
        max-height: 50%;
    }
}